@import url("https://fonts.googleapis.com/css?family=Lato:400,700&display=swap");
/*gradient for buttons: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);*/
/*gradient for text: (15deg, #1c78ad 0%, #72bbb2 100%)*/
@import url("https://fonts.googleapis.com/css?family=Zilla+Slab&display=swap");
@import url("https://fonts.googleapis.com/css?family=Big+Shoulders+Text:700&display=swap");

* {
  margin: 0;
  padding: 0;
}
#root {
  margin: 0;
  padding: 0;
}

@media (min-width: 0px) {
  body {
    // padding: 2.5vw;
    height: 100vh;
    background-image: linear-gradient(
      to top,
      #c4c5c7 0%,
      #dcdddf 52%,
      #ebebeb 100%
    );
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-family: "Zilla Slab", serif;
    font-size: 10px;
  }

  a {
    color: #598e89;
    text-decoration: none;
  }

  h1,
  h2,
  h3,
  h4 {
    font-weight: normal;
  }

  h1 {
    text-align: center;
    padding: 10px;
  }

  h2 {
    font-size: 1.6rem;
    text-align: center;
    font-weight: bold;
  }

  h4 {
    font-size: 1.2rem;
  }

  .navbar-logo {
    font-size: 1.4rem;
    color: black;
    font-family: "Big Shoulders Text", cursive;
  }

  .heading {
    font-weight: bold;
    font-size: 2.2rem;
    padding: 20px;
  }

  h5 {
    font-weight: normal;
    margin: 10px;
    text-align: center;
  }

  button {
    font-family: "Zilla Slab", serif;
    font-weight: bold;
    font-size: 1.2rem;
    border: none;
    border-radius: 100px;
    padding: 10px 15px;
    background-color: rgba(79, 82, 82, 0.99);
    color: white;
  }

  .cancel-btn {
    background-color: rgba(79, 82, 82, 0.85);
  }

  .delete-btn {
    background-color: rgba(255, 99, 71, 0.9);
  }

  .skew {
    transform: skew(-20deg);
    background-color: tomato;
    opacity: 0.9;
    display: flex;
    justify-content: center;
    margin: 20px 0 10px 0;
    h3 {
      transform: skew(20deg);
      color: white;
      padding: 10px 30px;
      font-size: 1.5rem;
      font-weight: bold;
    }
  }

  .coming-soon {
    opacity: 0.9;
  }

  // appts and practices components
  .appointmentCard,
  .practiceCard {
    display: flex;
    justify-content: space-between;
    background-color: whitesmoke;
    margin: 10px 20px;
    padding: 15px;
    // box-sizing: border-box;
    width: 80%;
    border: dotted #c4c5c7 2px;
  }

  .appointments,
  .practices {
    display: flex;
    align-items: center;
    padding: 20px 10px;
    flex-direction: column;

    button {
      margin: 20px 0 10px 0;
    }
  }

  .heading-in-app {
    padding-top: 10px;
  }

  .apptH4 {
    color: #598e89;
    font-weight: bold;
  }

  .apptLow {
    margin-bottom: 10px;
  }

  .dlt {
    color: tomato;
  }
  // indiv app and practice
  .indiv-appt-view {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .indiv-appt {
    margin: 20px 30px;
    padding: 20px;
    background-color: whitesmoke;
    border: dotted #c4c5c7 2px;
    max-width: 450px;
    div {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
    }
    button {
      margin: 10px;
      font-size: 1rem;
    }
  }
}

@media (min-width: 750px) {
  .skew h3 {
    font-size: 1.6rem;
  }
}
