}

/*gradient for buttons: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);*/
/*gradient for text: (15deg, #1c78ad 0%, #72bbb2 100%)*/
* {
  margin: 0;
  padding: 0; }

#root {
  margin: 0;
  padding: 0; }

