  .apptH4 {
    color: #598e89;
    font-weight: bold; }
  .apptLow {
    margin-bottom: 10px; }
  .dlt {
    color: tomato; }
  .indiv-appt-view {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .indiv-appt {
    margin: 20px 30px;
    padding: 20px;
    background-color: whitesmoke;
    border: dotted #c4c5c7 2px;
    max-width: 450px; }
    .indiv-appt div {
      display: flex;
