@media (min-width: 0) {
  .landing-wrapper {
    width: 100vw;
    height: 100vh; }
  .landing {
    height: 100vh;
    max-width: 1200px;
    margin: auto;
    display: flex;
    flex-direction: column; }
  .title {
    color: black;
    font-size: 3.2rem;
    width: 330px;
    padding-left: 20px;
    text-align: left;
    font-weight: bold; }
  .landing-title-image {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
    .landing-title-image img {
      max-width: 200px; }
  .button-container {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
    margin-bottom: 55px; }
  .home-btn {
    width: 140px;
    margin: 0 10px 10px 10px; }
  .arrow {
    position: absolute;
    left: 50%;
    top: 94%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .arrow span {
    display: block;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-bottom: 5px solid white;
    border-right: 5px solid white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    margin: -10px;
    -webkit-animation: animate 5s infinite;
            animation: animate 5s infinite; }
  .arrow span:nth-child(2) {
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s; }
  @-webkit-keyframes animate {
    0% {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(-8px, -8px);
              transform: rotate(45deg) translate(-8px, -8px); }
    30% {
      opacity: 1;
      -webkit-transform: rotate(45deg) translate(0px, 0px);
              transform: rotate(45deg) translate(0px, 0px); }
    100% {
      opacity: 1; } }
  @keyframes animate {
    0% {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(-8px, -8px);
              transform: rotate(45deg) translate(-8px, -8px); }
    30% {
      opacity: 1;
      -webkit-transform: rotate(45deg) translate(0px, 0px);
              transform: rotate(45deg) translate(0px, 0px); }
    100% {
      opacity: 1; } }
  .logo-text {
    color: #598e89;
    font-family: "Big Shoulders Text", cursive;
    font-size: 3.8rem;
    padding-bottom: 15px;
    margin: 25px;
    border-bottom: dotted #c4c5c7 2px; }
  .mission {
    font-size: 1.7rem;
    padding: 0 10px; }
  .tile-containers {
    position: relative;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#c4c5c7), color-stop(52%, #dcdddf), to(#ebebeb));
    background-image: linear-gradient(to top, #c4c5c7 0%, #dcdddf 52%, #ebebeb 100%);
    background-repeat: no-repeat;
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px; }
  .home-card {
    margin: 30px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .home-icon {
    width: 55px;
    height: auto;
    padding-bottom: 20px; }
  .safety {
